@charset "utf-8";


html{
    font-size: 12px;
}

body{
    background: #000;
    color:white;
    width: 100%;
}

.Movies{
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    gap: 10px;
}

._movie{
    display: flex;
    align-items: flex-end;
    /* width: 50%; */
    background: #252525;
    justify-content: space-between;
    align-items:center;
    margin: 0;
    flex:1 100%;

}
.movie_poster{
   width: 100px;
}
.movie_data{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    margin:0 5%;
}
.movie_title{
    color:#ffde00;
    font-size: 3rem;
    display: inline-block;
    width: 90%;
    margin: 10px 0;
    line-height: 3rem;
}
.movie_option{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.movie_year{
    display: inline-block;
    font-size: 1.5rem;
    line-height: 1.5rem;
    color: #a0a0a0;
    margin: 0;
}
.movie_genres{
    display: block;
    list-style: none;
    color: #919191;
    margin: 0;
}
.movie_genres li{
    display: inline-block;
    margin-left: 5px;
    line-height: 1.5rem;
}
.movie_summary{
    width: 90%;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: 10px;
}
